/* ===== Reset CSS ====== */

body {
    font-size: 16px;
    font-family: $lato;
    background-color: $white;
    font-weight: 400;
    color: #444;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

::-moz-selection {
    background: #222;
    color: $white;
}

::selection {
    background: #222;
    color: $white;
}

a {
    cursor: pointer;

    &:hover,
    &:focus {
        color: #4e63d7;
        outline: 0 none;
        text-decoration: none;
    }
}
:active, :focus {
  outline: none !important;
}
ul,
li {
    text-decoration: none;
    margin: 0;
    padding: 0;
    list-style: none;
}

p {
    line-height: 26px;
    margin-top: 0;
    margin-bottom: 15px;
}

span {
    color: inherit;
}

i {
    line-height: 1;
    font-style: normal;
}

img {
    border-style: none;
    height: auto;
    max-width: 100%;
    vertical-align: middle;
}


/* ===== Headings ====== */

h1,
h2,
h3,
h4,
h5,
h6 {
  color:$c-black;
  font-family: $lato;
  font-weight: 700;
  margin-top: 0;
  margin-bottom: 15px;
  line-height: 1.33;
  text-transform: capitalize;
}

h1 {
  font-size: 45px;
  @include media-breakpoint-up(lg){
    font-size: 54px;
  }
}

h2 {
  font-size: 38px;
  @include media-breakpoint-up(lg){
    font-size: 45px;
  }
}

h3 {
  font-size: 30px;
  margin-bottom:30px;
  @include media-breakpoint-up(lg){
    font-size: 35px;
  }
}

h4 {
  font-size: 25px;
}

h5 {
  font-size: 21px;
}

h5 a,
h6 a {
  transition: all .3s ease;
}
h5{
  a{
    color: inherit;
  }
}
h5 {
  &:hover {
    a {
      color: $red;
    }
  }
}


h6 {
  font-size: 16px;
  font-weight: 600;
  text-transform: capitalize;
}


/* ===== Custom CSS ====== */

.c-white {
    color: $white !important;
}

.c-black {
    color: $c-black !important;
}

.c-blue {
  color: $red !important;
}

.gray {
    color: $c-gray !important;
}

.sp-100 {
    padding-top: 80px;
    padding-bottom: 80px;

    @include media-breakpoint-up(lg) {
        padding-top: 100px;
        padding-bottom: 100px;
    }
}

.sp-100-70 {
    padding-top: 80px;
    padding-bottom: 50px;

    @include media-breakpoint-up(lg) {
        padding-top: 100px;
        padding-bottom: 70px;
    }
}

.sp-100-40 {
  padding-top: 80px;
  padding-bottom: 20px;

  @include media-breakpoint-up(lg) {
    padding-top: 100px;
    padding-bottom: 40px;
  }
}

.mb-30 {
    margin-bottom: 30px;
}
.mt-30 {
  margin-top: 30px;
}


.mb-60 {
  margin-bottom: 60px;
}
.bg-light {
    background-color: $light-bg !important;
}

.br-10 {
    border-radius: 10px;
}

.shadow{
  box-shadow:  0px 1px 30px 1px rgba(0, 0, 0, 0.05) !important;
}
.slick-slide {
  height:auto;
}


/* ===== Custom Button ====== */
.btn {
  font-family: $lato;
  text-transform: capitalize;
  padding: 8px 25px;
  border-radius: 5px;
  border: 0;
  font-size: 16px;
  font-weight: 600;
  color: $white;
  position: relative;
  z-index: 0;
  background-color: $red;
  transition: all 0.3s ease;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.15);

  &:after {
      content: "";
      display: inline-block;
      height: 100%;
      width: 100%;
      border-radius: 5px;
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
      background-color: $red;
      opacity: 0.4;
    }

   &:hover::after {
      transform: scaleX(1.4) scaleY(2);
      opacity: 0;
     background-color: $white;
     transition: all 0.3s linear;
   }
   &:hover{
     color: #fff;
   }
}
.btn-lg {
  padding: 12px 40px;
  font-size:18px;
}
.btn-light{
  background-color: $white;
  color: $red;
  &:after {
    background-color: $white;
  }
  &:hover{
    color: $red;
    background-color: $white;
  }
}
.btn-border{
  background-color: transparent;
  border: 1px solid $c-black;
  color: $c-black;
  &:after {
    background-color:$white;
  }
  &:hover::after {
    background-color: $c-black;
  }
  &:hover{
    color:$white;
    background-color:$c-black;
  }
}
.btn-black{
  background-color:$c-black;
  color: $white;
  &:after {
    background-color:$c-black;
  }
}


/* ===== Navbar Toggle ====== */
.navbar-toggler{
  height: 2.5em;
  width: 2.5em;
  position: relative;
  font-size: 12px;
  cursor: pointer;
  transition: all .2s;
  .navbar-toggler-lines {
    &,
    &:after,
    &:before {
      pointer-events: none;
      display: block;
      content: "";
      width: 100%;
      background-color: $c-black;
      height: .25em;
      position: absolute;
      transform: rotate(0);
      transition: all .2s;
    }
    &:after {
      left: 0;
      top: -0.8em;
      width:30px;
    }
    &:before {
      left:0;
      top:0.8em;
      width:30px;
    }
  }
  &[aria-expanded="true"]{
    .navbar-toggler-lines {
      &{
        background-color: initial;
        transform: translateX(-2em);
      }
      &:before {
        width:2em;
        left: .5em;
        top: 0;
        transform: translateX(2em) rotate(135deg);
      }
      &:after {
        width:2em;
        left: .5em;
        top: 0;
        transform: translateX(2em) rotate(-135deg);
      }
    }
  }
}

/* ===== Section Title  ====== */
.sec-title{
  text-align: center;
  h3{
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
  }
  p{
    max-width: 550px;
    margin: 0 auto;
  }
}


/* ===== Breadcrumb-Section ===== */
.breadcrumb-section {
  padding: 150px 0 100px;
  position: relative;
  overflow: hidden;
  z-index: 0;
  background-color: #fafbff;

  &::after{
    position: absolute;
    content: '';
    top: 0;
    bottom: 0;
    height: 100%;
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url(../img/pages/banner-bg.png);
    opacity: 0.05;
  }

  @include media-breakpoint-up(md) {
    padding: 150px 0 100px;
  }
  h2 {
    font-size: 35px;
    @include media-breakpoint-up(md) {
      font-size: 38px;
    }
  }
  .container{
    position: relative;
    z-index: 1;
  }
}

//Breadcrumb
.breadcrumb{
  background-color: transparent;
  padding: 0;

  .breadcrumb-item{
    text-transform: capitalize;
    font-weight: 600;
    font-size: 18px;
    + .breadcrumb-item {
      padding-left: 15px;
    }
    a,
    &.active,
    &:before{
      color: $c-black;
    }
    &:before {
      padding-right: 15px;
    }
    &.active{
      color: $red;
    }
  }
}



