/* ===== Hero Section ===== */

.section-hero .banner {
  padding: 190px 0 125px;
  background-color: $white;

  .banner-text {
    p{
      font-size: 18px;
    }
  }
}

/* ===== Clients Section ===== */

.clients{
  padding: 70px 0;
}
.client-img{
  text-align: center;
  img{
    width: 170px;
    margin: 0 auto;
  }
}

/* ===== SAAS Features Section ===== */

.list1{
  li {
    position: relative;
    padding-left: 25px;
    &:before{
      content: '\f269';
      position: absolute;
      left: 0;
      font-family: Material-Design-Iconic-Font;
      color:#00b15c;
    }
  }
}

/* ===== Features Section ===== */

.feature-box{
  padding: 35px  40px;

  .icon {
    position: relative;
    z-index: 0;
    padding: 30px 0 10px;
    margin-bottom: 35px;

    &:before{
      position: absolute;
      content: '';
      left: 50%;
      top:60%;
      height: 100px;
      width: 110px;
      z-index: -1;
      background-image: url(../img/svg/icon-bg.svg);
      background-size: contain;
      background-repeat: no-repeat;
      transform: translate(-50%, -50%);
    }
    i {
      font-size: 50px;
      color: $red;
      position: relative;
    }
  }
}


/* ===== Testimonial Section ===== */

.testimonial-item{
  padding: 35px;
  background-color: $white;
  height: auto;
  border-radius: 10px 10px 10px 50px ;
  box-shadow: 0px 1px 20px 1px rgba(0, 0, 0, 0.05) !important;
  margin: 15px;
  position: relative;
  text-align: left;

  &:after{
    position: absolute;
    right: 22px;
    top: 0;
    content: '\F1B2';
    font-family: Material-Design-Iconic-Font;
    font-size: 80px;
    line-height: 1;
    color: $red;
    opacity: 0.1;
  }

  .img-holder{
    img{
      height: 95px;
      border-radius: 50%;
    }
  }
  .rating {
    font-size: 20px;
  }
}
.testimonial-slider {
  .slick-dots {
    position: relative;
    bottom: auto;
    display: inline-block;
    width: auto;
    line-height: 1;
    font-size: 0;
    margin-top: 30px;

    li {
      position: relative;
      display: inline-block;
      width: 16px;
      height: 16px;
      margin: 5px;
      border: 1px solid $red;
      border-radius: 50px;
      transition: all 0.3s ease;

      &.slick-active {
        background-color: $red;
      }

      button {
        display: none;

        &:before {
          display: none;
        }
      }
    }
  }
}


/* ===== Video Section ===== */

.video-btn{
  height: 58px;
  width: 58px;
  border-radius: 5px;
  background-color: $white;
  position: absolute;
  bottom: 15px;
  right: 15px;

  i{
    font-size: 40px;
    color: $red;
    padding-top: 10px;
  }
}


/* ===== CTA Section ===== */
.cta-section{
  background-image: url(../img/home/cta-bg.png);
  padding: 80px 0 40px;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}


