/* ===== Pricing Section ===== */

.container-scroll > .row {
  overflow-x: auto;
  white-space: nowrap;
}

.price-tabs{
  a{
    border: 1px solid $c-black;
    color: $c-black;
    font-weight:500;
    font-size: 20px;
    padding: 10px 50px;

    &:hover{
      color: $c-black;
    }
    &.active{
      background-color: $c-black;
      color: $white;
    }

    &:first-child{
       border-bottom-left-radius: 30px;
    }
    &:last-child{
      border-top-right-radius: 30px;
    }
  }
}

.pricing-table {
  text-align: center;
  &.border{
    border-right: 0 !important;
  }
  .rate {
    padding: 18px 0;
    sup {
      top: 13px;
      left: 5px;
      font-size: 0.35em;
      font-weight: 500;
      vertical-align: top;
    }

    sub {
      font-size: 0.35em;
      color: $red;
      left: -7px;
      bottom: 0;
    }
  }

  &.price-free{
    .price-head{
      background-color: #00a3ef;
    }
  }
  &.price-standard{
    .price-head{
      background-color: #efbf01;
    }
  }
  &.price-pro{
    box-shadow: 0 1px 30px 1px rgba(0, 0, 0, 0.1) !important;
    border:2px solid #04b532;
    .price-head{
      background-color: #04b532;
    }
  }
  &.price-enterprise{
    .price-head{
      background-color: #ff0042;
    }
  }
}
.overflow-x-auto{
  overflow-x: auto;
}

.price-content{
  ul{
    padding: 10px 20px;
  }
  li{
    padding:10px 0;
    @include media-breakpoint-up(lg){
      padding:10px;
    }
  }
  .blue{
    color: #00a3ef;
  }
}


@include media-breakpoint-down(lg) {
  .price-wrap {
    width: 100%;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
  }
}


/* ===== FAQ Section ===== */

.theme-accordion {

  .card-header {
    background-color: transparent;
    button {
      padding: 10px;
      padding-left: 60px;
      font-weight: 600;
      font-size: 16px;
      background-color: $red;
      color: $white;
      border: 1px solid transparent;
      border-radius: 10px;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;

      &:before {
        font-family: "Material-Design-Iconic-Font";
        content: '\F2FC';
        height: 100%;
        width: 50px;
        position: absolute;
        top: 0;
        left: 0;
        text-align: center;
        padding-top: 10px;
      }

      &.collapsed {
        &:before {
          content: '\f2f9';
        }
        background-color: $white;
        color: $c-black;
        border-color: #d7d7d7;
        border-bottom-left-radius:10px;
        border-bottom-right-radius: 10px;
      }
    }
  }
  .card-body {
    padding: 30px 30px 15px;
    border: 1px solid #d7d7d7;
    border-radius: 0 0 10px 10px;
    border-top: 0;
  }
}


.row-scroll > .col-md-3{
  min-width: 150px;
  &:last-of-type{
    .pricing-table{
      border-right: 1px solid #dee2e6!important;
    }
  }
}
.price-top.title{
  h3{
    padding: 30px 20px;
    margin-bottom: 0;
    @include media-breakpoint-up(lg){
      padding: 48px 30px;
    }
  }
}




