/* ===== Navbar ====== */

.navigation-bar{
  padding: 25px 0;
  background-color:transparent;
  transition: all 0.3s ease;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 99;
}
.navbar {
  .navbar-nav {
    .nav-link {
      color: $c-black;
      font-weight: 600;
      padding: 8px 0;
    }
  }
  .logo {
    width: 150px;
  }
}

.nav-item {
  padding-right: 35px;
}

.navigation-bar{
  &.sticky {
    box-shadow: 0 0 20px -7px rgba(0, 0, 0, 0.3);
    padding: 15px 0;
    background-color: $white;
  }
}

@include media-breakpoint-down(md){
  .navbar-collapse{
    background-color: #eef7ff;
    padding: 20px;
    margin-top: 20px;
  }
}


