
/* ===== Features ===== */
.saas-f-box{
  .icon{
    height: 90px;
    width: 90px;
    border-radius: 50%;
    text-align: center;
    padding-top: 21px;
    margin-bottom: 20px;
    background-color: $white;
    box-shadow: 0px 1px 30px 1px rgba(0, 0, 0, 0.1);

    i{
      font-size: 50px;
    }
  }
}


/* ===== Integration Box ===== */

.integrate-box{
  padding: 30px 15px 20px;
  width: 180px;
  position: relative;
  text-align: center;
  background-color: $white;
  border-radius: 50px 50px 10px 10px;
  margin: 0 auto 15px;

  &:after{
    position: absolute;
    content: '';
    height: 120px;
    width: 180px;
    background-color:#def3ff;
    opacity: 0.5;
    border-radius: 50px 50px 50% 50%;
    left: 0;
    top: 0;
  }
  img{
    margin-bottom: 40px;
    position: relative;
    z-index: 5;
  }
}


/* ===== Clients ===== */

.clients-bg{
  position: relative;
  z-index: 0;

  &::after{
    position: absolute;
    content: '';
    top: 0;
    bottom: 0;
    height: 100%;
    width: 100%;
    background-size: cover;
    background-attachment: fixed;
    background-image: url("../img/pages/clients-bg.png");
    opacity: 0.08;
    z-index: -1;
  }
  .client-img {
    img {
      width: auto;
      height: 40px;
    }
  }
}


/* ===== Responsive ===== */

.app-responsive{
  .stores-icon{
    img{
      width: 150px;
    }
  }
}


