/* ===== Footer ===== */
.footer{
  .logo{
    width: 145px;
    display: block;
  }
  .foot-links{
    a{
      color:$c-black;
      padding-left: 15px;
      position: relative;
      transition: all 0.3s ease;
      display: block;
      margin-bottom: 15px;
      text-transform: capitalize;
      &:last-of-type{
        margin-bottom: 0;
      }

      &:before {
        font-size: 17px;
        content: '\f2fb';
        font-family: $material-font;
        position: absolute;
        top:2px;
        left: 0;
        line-height: 1;
        transition: all 0.3s ease;
        color:$c-black;
      }
      &:hover{
        padding-left: 20px;
      }
    }
  }
  .stores-icon{
    a{
      width: 110px;
      display: inline-block;
    }
  }
}
.contact-info{
  li{
    position: relative;
    padding-left: 25px;
    margin-bottom: 15px;
    color:$c-black;

    &:last-of-type{
      margin-bottom: 0;
    }

    i{
      left: 0;
      top: 4px;
      position: absolute;
      color:$c-black;
    }
  }
}
.socials{
  a{
    font-size: 20px;
    margin-right: 3px;
    color: #fff;
    transition: all 0.3s ease;
    height: 30px;
    width: 30px;
    background-color: $c-black;
    display: inline-block;
    padding: 6px 0;
    text-align: center;

    &:hover{
      transform: translateY(3px);
      color: $white;
    }
  }
}


